import Vue from "vue"
import Router from "vue-router"
import Home from "@/components/Home"
import Leaderboard from "@/components/Leaderboard"
import Compare from "@/components/Compare"

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: "/",
      name: "Home",
      component: Home
    },
    {
      path: "/leaderboard",
      name: "Leaderboard",
      component: Leaderboard,
      props: true
    },
    {
      path: "/compare",
      name: "Compare",
      component: Compare,
      props: true
    }
  ]
})



