<template>
  <div>
    <section>
      <article class="form">
        <ul>
          <li>
            <div>
              <label for="player_ids" class="text">Player Ids:</label>
              <textarea id="player_ids" v-model="inputPlayers" value="" placeholder="Please input a list of steam account ids, separate by ," class="player_ids" />
              <div>
                <span class="text">{{limit_message}}</span>
              </div>
            </div>
          </li>
          <li>
            <div>
              <label for="period" class="text">Select Period:</label>
              <select id="period" v-model="period">
                <option value="7">Last Week</option>
                <option value="30">Last Month</option>
                <option value="365">Last Year</option>
              </select>
            </div>
          </li>
          <li>
            <div>
              <label for="use_pro_data" class="text">Use Pro Player Data</label>
              <input id="use_pro_data" type="checkbox" v-model="use_pro_data">
            </div>
          </li>
          <li>
            <div>
              <button @click="view('leaderboard')">View Leaderboard</button>
              <button @click="view('compare')">Compare</button>
            </div>
          </li>
        </ul>
      </article>
    </section>
  </div>
</template>

<script>
import axios from "axios"
import config from "../config"
import * as func from "../helper/functions"

export default {
  name: "Home",
  data() {
    return {
      env: process.env.NODE_ENV,
      inputPlayers: process.env.NODE_ENV == "development" && "3940262,88470,80395370,177941583,341143789,43411656,103585724,103585724,12231202,84105632",
      period: 7,
      use_pro_data: false,
      players: {},
      limit_message: `Only the first ${config.GET_LIMIT} players are used.`
    }
  },
  mounted() {
    func.toggleLoading()
  },
  methods: {
    async view(action) {
      // player id validation
      let playerIds = this.inputPlayers
        .split(",")
        .filter(playerId => Number.isInteger(parseInt(playerId)))
        .filter((item, i, ar) => ar.indexOf(item) === i)
      if (playerIds.length < 2) {
        // TODO display errors better than alerts
        return alert("Please input at least two valid player ids")
      }
      playerIds = playerIds.slice(0, config.GET_LIMIT)
      if (this.use_pro_data) {
        const proPlayerData = await axios.get("https://api.opendota.com/api/proPlayers")
        playerIds = proPlayerData.data.map(value => value.account_id).slice(0, config.GET_LIMIT)
      }
      if (playerIds.length >= config.API_RPS_LIMIT) {
        return alert("We're likely to exceed the request per second limit (3). I'd rather stop you now.")
      }
      switch (action) {
        case "leaderboard":
          this.$router.push({
            name: "Leaderboard",
            params: { playerIds: playerIds, period: this.period, useProData: this.use_pro_data }
          })
          break
        case "compare":
          this.$router.push({
            name: "Compare",
            params: { playerIds: playerIds, period: this.period, useProData: this.use_pro_data }
          })
          break
      }
      // debugger
    }
  }
}
</script>

<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0;
}
a {
  color: #42b983;
}
.player_ids {
  margin: 0;
  width: 400px;
  height: 100px;
}
article.form,
article.form li {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  width: 800px;
  margin: 0 auto;
}
</style>



