<template>
    <div>
        <span class="se-pre-con hide">
            <div class="center_text text">Loading player data please wait. <span v-if="useProData" class="text">Using Pro Player Data</span></div>
            <div class="center_text loading_message text">{{loadingMessage}}</div>
        </span>
        <div :class="{show: isLoading < 1}" class="hide">
          <section>
            <header>
                Player comparisons with data from the last {{period}} days. <a href="#" @click="backHome()">Back to Home</a>
                <div v-if="useProData">Using Pro Player data</div>
            </header>
            <article>
              <table class="sortable" cellpadding="2" cellspacing="0" bordercolor="#3b3a38" border="2">
                  <thead>
                      <tr>
                          <td>Player</td>
                          <td class="clickable" :class="{sorted:sortBy=='matches'}" @click="sort('matches')">Matches<span v-if="sortBy=='matches'" :class="{desc: order == 'desc'}" class="sign arrow"></span></td>
                          <td class="clickable" :class="{sorted:sortBy=='win'}" @click="sort('win')">Won<span v-if="sortBy=='win'" :class="{desc: order == 'desc'}" class="sign arrow"></span></td>
                          <td class="clickable" :class="{sorted:sortBy=='lose'}" @click="sort('lose')">Lost<span v-if="sortBy=='lose'" :class="{desc: order == 'desc'}" class="sign arrow"></span></td>
                          <td class="clickable" :class="{sorted:sortBy=='winRate'}" @click="sort('winRate')">Win Rate<span v-if="sortBy=='winRate'" :class="{desc: order == 'desc'}" class="sign arrow"></span></td>
                          <td class="clickable" :class="{sorted:sortBy=='kills'}" @click="sort('kills')">Kills<span v-if="sortBy=='kills'" :class="{desc: order == 'desc'}" class="sign arrow"></span></td>
                          <td class="clickable" :class="{sorted:sortBy=='deaths'}" @click="sort('deaths')">Deaths<span v-if="sortBy=='deaths'" :class="{desc: order == 'desc'}" class="sign arrow"></span></td>
                          <td class="clickable" :class="{sorted:sortBy=='assists'}" @click="sort('assists')">Assists<span v-if="sortBy=='assists'" :class="{desc: order == 'desc'}" class="sign arrow"></span></td>
                          <td class="clickable" :class="{sorted:sortBy=='kda'}" @click="sort('kda')">KDA<span v-if="sortBy=='kda'" :class="{desc: order == 'desc'}" class="sign arrow"></span></td>
                          <td class="clickable" :class="{sorted:sortBy=='neutral_kills'}" @click="sort('neutral_kills')">CS<span v-if="sortBy=='neutral_kills'" :class="{desc: order == 'desc'}" class="sign arrow"></span></td>
                          <td class="clickable" :class="{sorted:sortBy=='denies'}" @click="sort('denies')">Denies<span v-if="sortBy=='denies'" :class="{desc: order == 'desc'}" class="sign arrow"></span></td>
                          <td class="clickable" :class="{sorted:sortBy=='actions_per_min'}" @click="sort('actions_per_min')">APM<span v-if="sortBy=='actions_per_min'" :class="{desc: order == 'desc'}" class="sign arrow"></span></td>
                      </tr>
                  </thead>
                  <tbody>
                      <template v-for="(player, key) in arrayPlayers">
                          <template v-if="player.info && player.stats">
                              <tr v-bind:key="key" :class="key%2 !== 0 || 'alternative'">
                                  <td class="info">
                                      <span>
                                          <a class="player_link" :href="player.info.profile.profileurl">
                                              <img class="avatar" :src="player.info.profile.avatar" />
                                              <span class="name">{{player.info.profile.personaname}}</span>
                                          </a>
                                          <img class="flag" v-if="player.info.profile.loccountrycode" :src="'http://steamcommunity-a.akamaihd.net/public/images/countryflags/' + player.info.profile.loccountrycode.toLowerCase() + '.gif'" />
                                      </span>
                                  </td>
                                  <td>
                                      {{player.stats.kills.n}}
                                  </td>
                                  <td class="cell-xlarge">
                                      <span :class="{green:top.won.playerId == player.playerId, red: bottom.won.playerId == player.playerId}">
                                        {{player.wl.win}}
                                      </span>
                                  </td>
                                  <td>
                                      <span :class="{green:top.lost.playerId == player.playerId, red: bottom.lost.playerId == player.playerId}">
                                        {{player.wl.lose}}
                                      </span>
                                  </td>
                                  <template>
                                      <td v-if="player.wl.winRate != -1">
                                        <span class="winrate_text">
                                            <span :class="{green:top.winRate.playerId == player.playerId, red: bottom.winRate.playerId == player.playerId}">
                                                {{player.wl.winRate}}%
                                            </span>
                                        </span>
                                        <div class="winrate"><div :style="{width: player.wl.winRate+'%'}"></div></div>
                                      </td>
                                      <td v-else>No matches in chosen period</td>
                                  </template>
                                  <td>
                                      <span :class="{green:top.kills.playerId == player.playerId, red: bottom.kills.playerId == player.playerId}">
                                        {{player.stats.kills.sum}}
                                      </span>
                                  </td>
                                  <td>
                                      <span :class="{green:top.deaths.playerId == player.playerId, red: bottom.deaths.playerId == player.playerId}">
                                        {{player.stats.deaths.sum}}
                                      </span>
                                  </td>
                                  <td>
                                      <span :class="{green:top.assists.playerId == player.playerId, red: bottom.assists.playerId == player.playerId}">
                                        {{player.stats.assists.sum}}
                                        </span>
                                  </td>
                                  <td>
                                      <span :class="{green:top.kda.playerId == player.playerId, red: bottom.kda.playerId == player.playerId}">
                                        {{player.stats.kda.sum}}
                                      </span>
                                  </td>
                                  <td>
                                      <span :class="{green:top.neutral_kills.playerId == player.playerId, red: bottom.neutral_kills.playerId == player.playerId}">
                                        {{player.stats.neutral_kills.sum}}
                                      </span>
                                  </td>
                                  <td>
                                      <span :class="{green:top.denies.playerId == player.playerId, red: bottom.denies.playerId == player.playerId}">
                                        {{player.stats.denies.sum}}
                                      </span>
                                  </td>
                                  <td>
                                      <span :class="{green:top.actions_per_min.playerId == player.playerId, red: bottom.actions_per_min.playerId == player.playerId}">
                                        {{player.stats.actions_per_min.sum}}
                                      </span>
                                  </td>
                              </tr>
                          </template>
                          <template v-else> <!-- Also display invalid/not found users to let submitter know what's wrong -->
                              <tr v-bind:key="key" :class="key%2 !== 0 || 'alternative'">
                                  <td class="info" colspan="12">
                                      Player ID {{player.playerId}} not found
                                  </td>
                              </tr>
                          </template>
                      </template>
                  </tbody>
              </table>
            </article>
          </section>
        </div>
    </div>
</template>

<script>
import axios from "axios"
import config from "../config"
import * as func from "../helper/functions"
export default {
  name: "Compare",
  data() {
    return {
      env: process.env.NODE_ENV,
      config: config,
      loadingMessage: "",
      isLoading: false,
      dictPlayers: {},
      arrayPlayers: [],
      top: {
        won: {},
        lost: {},
        winRate: {},
        kills: {},
        deaths: {},
        assists: {},
        kda: {},
        neutral_kills: {},
        denies: {},
        actions_per_min: {}
      },
      bottom: {
        won: {},
        lost: {},
        winRate: {},
        kills: {},
        deaths: {},
        assists: {},
        kda: {},
        neutral_kills: {},
        denies: {},
        actions_per_min: {}
      },
      sortBy: "winRate",
      order: "desc"
    }
  },
  props: ["playerIds", "period", "useProData"],
  mounted: async function() {
    if (!this.$props.playerIds) {
      return this.backHome()
    }
    this.isLoading = true
    if (this.useProData) {
    }
    func.toggleLoading()
    axios.interceptors.response.use(
      response => {
        this.loadingMessage = `Receiving player ${response.config.playerId}`
        return response
      },
      error => {
        this.loadingMessage = `Failed receiving data`
        return Promise.reject(error)
      }
    )
    const { playerIds } = this

    // get player info
    const playerInfoResponses = await axios.all(
      playerIds.map(playerId =>
        axios.get(`${config.OPENDOTA_API}/players/${playerId}`, {
          playerId,
          validateStatus: status => {
            if (status == 429) {
              this.loadingMessage = `Failed receiving data`
            }
            return status < 500 // Reject only if the status code is greater than or equal to 500
          }
        })
      )
    )
    playerInfoResponses.forEach(response => {
      this.dictPlayers[response.config.playerId] = {
        playerId: response.config.playerId
      }
      this.dictPlayers[response.config.playerId].info = response.data.profile ? response.data : null
    })
    // get win rate
    const wlResponses = await axios.all(
      this.playerIds.reduce((requests, playerId) => {
        if (this.dictPlayers[playerId].info) {
          requests.push(axios.get(`${config.OPENDOTA_API}/players/${playerId}/wl?date=${this.period}`, { playerId }))
        } else {
          this.dictPlayers[playerId].wl = {
            winRate: -2,
            win: 0,
            lose: 0
          }
        }
        return requests
      }, [])
    )
    wlResponses.forEach(response => {
      // build leaderboard
      let winRate
      if (response.data.win === 0 && response.data.lose === 0) {
        winRate = -1
      } else if (response.data.lose === 0) {
        winRate = 100
      } else {
        winRate = parseFloat((response.data.win / (response.data.win + response.data.lose) * 100).toFixed(3))
      }
      this.dictPlayers[response.config.playerId].wl = {
        winRate,
        win: response.data.win,
        lose: response.data.lose
      }
    })
    // get player stats
    const playerStatsResponses = await axios.all(
      this.playerIds.reduce((requests, playerId) => {
        if (this.dictPlayers[playerId].info) {
          requests.push(axios.get(`${config.OPENDOTA_API}/players/${playerId}/totals?date=${this.period}`, { playerId }))
        } else {
          // error
        }
        return requests
      }, [])
    )
    playerStatsResponses.forEach(response => {
      this.dictPlayers[response.config.playerId].stats = {}
      response.data.forEach((stat, index) => {
        this.dictPlayers[response.config.playerId].stats[stat.field] = stat
      })
    })
    this.loadingMessage = "Killing dragons"
    this.arrayPlayers = Object.keys(this.dictPlayers).map(key => this.dictPlayers[key])
    this.sort("winRate", false)
    for (var i = 0; i < this.arrayPlayers.length; ++i) {
      let player = this.arrayPlayers[i]
      if (!player.info) continue
      const { playerId } = player
      if (player.wl.winRate == -1 || player.wl.winRate == -2) {
        continue
      }
      // set top
      if (!this.top.won.sum || player.wl.win > this.top.won.sum) {
        this.top.won = {
          playerId: playerId,
          sum: player.wl.win
        }
      }
      if (!this.top.lost.sum || player.wl.lose > this.top.lost.sum) {
        this.top.lost = {
          playerId: playerId,
          sum: player.wl.lose
        }
      }
      if (!this.top.winRate.sum || player.wl.winRate > this.top.winRate.sum) {
        this.top.winRate = {
          playerId: playerId,
          sum: player.wl.winRate
        }
      }
      // set bottom
      if (!this.bottom.won.sum || player.wl.win < this.bottom.won.sum) {
        this.bottom.won = {
          playerId: playerId,
          sum: player.stats.kills.sum
        }
      }
      if (!this.bottom.lost.sum || player.wl.lose < this.bottom.lost.sum) {
        this.bottom.lost = {
          playerId: playerId,
          sum: player.stats.kills.sum
        }
      }
      if (!this.bottom.winRate.sum || player.wl.winRate < this.bottom.winRate.sum) {
        this.bottom.winRate = {
          playerId: playerId,
          sum: player.wl.winRate
        }
      }
      this.compareStat(player, "kills")
      this.compareStat(player, "deaths")
      this.compareStat(player, "assists")
      this.compareStat(player, "kda")
      this.compareStat(player, "neutral_kills")
      this.compareStat(player, "denies")
      this.compareStat(player, "actions_per_min")
    }
    this.isLoading = false
    func.toggleLoading()
    // debugger
  },
  methods: {
    backHome() {
      this.$router.push({ name: "Home" })
      return false
    },
    sort(type, reverseOrder = true) {
      this.sortBy = type
      if (this.order == "desc") {
        this.order = reverseOrder ? "asc" : this.order
      } else {
        this.order = reverseOrder ? "desc" : this.order
      }
      switch (type) {
        case "winRate":
        case "win":
        case "lose":
          this.arrayPlayers = this.arrayPlayers.sort((a, b) => {
            return a.wl[type] - b.wl[type]
          })
          break
        case "matches":
          this.arrayPlayers = this.arrayPlayers.sort((a, b) => {
            if (a.info == undefined) {
              return -1
            }
            if (b.info == undefined) {
              return 1
            }
            return a.stats.kills.n - b.stats.kills.n
          })
          break
        default:
          this.arrayPlayers = this.arrayPlayers.sort((a, b) => {
            if (a.info == undefined) {
              return -1
            }
            if (b.info == undefined) {
              return 1
            }
            return a.stats[type].sum - b.stats[type].sum
          })
      }
      if (this.order == "desc") {
        this.arrayPlayers.reverse()
      } else {
        this.arrayPlayers = this.arrayPlayers.sort((a, b) => {
          if (a.wl.winRate == -1 || a.wl.winRate == -2) {
            return 1
          }
          if (b.wl.winRate == -1 || b.wl.winRate == -2) {
            return 1
          }
          return -1
        })
      }
    },
    compareStat(player, field, getTop = true) {
      const { playerId } = player
      if (this.top[field].sum == undefined) {
        this.top[field] = {
          sum: 0,
          playerId: 0
        }
      }
      if (this.bottom[field].sum == undefined) {
        this.bottom[field] = {
          sum: 99999,
          playerId: 0
        }
      }
      if (player.stats[field].sum > this.top[field].sum) {
        this.top[field] = {
          playerId: playerId,
          sum: player.stats[field].sum
        }
      } else if (player.stats[field].sum < this.bottom[field].sum) {
        this.bottom[field] = {
          playerId: playerId,
          sum: player.stats[field].sum
        }
      }
    }
  },
  computed: {}
}
</script>
<style scoped>
.green {
  background: #3bb835;
  padding: 0 5px;
}
.red {
  background: #ce3d36;
  padding: 0 5px;
}
table .cell-xlarge {
  width: 200px;
}
</style>



