export function insert(element, array, field) {
  array.splice(locationOf(element, array, field) + 1, 0, element)
  return array
}

function locationOf(element, array, field, start, end) {
  if (array.length === 0) return -1
  start = start || 0
  end = end || array.length
  var pivot = parseInt(start + (end - start) / 2, 10)
  if (array[pivot][field] === element[field]) return pivot
  if (end - start <= 1) {
    return array[pivot][field] < element[field] ? pivot - 1 : pivot
  }
  if (array[pivot][field] > element[field]) {
    return locationOf(element, array, field, pivot, end)
  } else {
    return locationOf(element, array, field, start, pivot)
  }
}

export function toggleLoading(oncomplete) {
  $(".se-pre-con").fadeToggle("slow", oncomplete)
  $(".se-pre-con").toggleClass("hide")
}

export function getRandomInt(min, max) {
  return Math.floor(Math.random() * (max - min + 1)) + min
}



