<template>
    <div>
        <span class="se-pre-con hide">
            <div class="center_text text">{{titleMessage}}.</div>
            <div class="center_text loading_message text">{{loadingMessage}}</div>
        </span>
        <div :class="{show: leaderboard.length > 0}" class="hide">
          <section>
            <header>Leaderboard for up to {{config.GET_LIMIT}} players with matches in the last 
              <select v-model="from">
                <option value="7">Last Week</option>
                <option value="30">Last Month</option>
                <option value="365">Last Year</option>
              </select> days. <a href="#" @click="backHome()">Back to Home</a>
              <div v-if="useProData">Using Pro Player data</div>
            </header>
            <article>
              <table class="leaderboard sortable" cellpadding="2" cellspacing="0" bordercolor="#3b3a38" border="2">
                  <thead>
                      <tr>
                          <td>#</td>
                          <td>Player</td>
                          <td class="clickable" @click="sort('winRate')">Win Rate<span v-if="sortBy=='winRate'" :class="{desc: order == 'desc'}" class="sign arrow"></span></td>
                          <td class="clickable" @click="sort('won')">Won<span v-if="sortBy=='won'" :class="{desc: order == 'desc'}" class="sign arrow"></span></td>
                          <td class="clickable" @click="sort('lost')">Lost<span v-if="sortBy=='lost'" :class="{desc: order == 'desc'}" class="sign arrow"></span></td>
                      </tr>
                  </thead>
                  <tbody>
                      <template v-for="(player, key) in paginate">
                          <template v-if="players[player.playerId].info">
                              <tr v-bind:key="player.playerId" :class="key%2 !== 0 || 'alternative'">
                                  <td>{{key+1+(currentPage*itemsPerPage - itemsPerPage)}} <span v-if="env == 'development'">(<a :href="`https://api.opendota.com/api/players/${player.playerId}`">{{player.playerId}}</a>)</span></td>
                                  <td class="info">
                                      <span>
                                          <a class="player_link" :href="players[player.playerId].info.profile.profileurl">
                                              <img class="avatar" :src="players[player.playerId].info.profile.avatar" />
                                              <span>{{players[player.playerId].info.profile.personaname}}</span>
                                          </a>
                                          <img class="flag" v-if="players[player.playerId].info.profile.loccountrycode" :src="'http://steamcommunity-a.akamaihd.net/public/images/countryflags/' + players[player.playerId].info.profile.loccountrycode.toLowerCase() + '.gif'" />
                                      </span>
                                  </td>
                                  <template>
                                      <td v-if="player.winRate != -1">
                                        <span class="winrate_text">{{player.winRate}}%</span>
                                        <div class="winrate"><div :style="{width: player.winRate+'%'}"></div></div>
                                      </td>
                                      <td v-else>No matches in chosen period</td>
                                  </template>
                                  <td>
                                      {{players[player.playerId].wl.win}}
                                  </td>
                                  <td>
                                      {{players[player.playerId].wl.lose}}
                                  </td>
                              </tr>
                          </template>
                          <template v-else> <!-- Also display invalid/not found users to let submitter know what's wrong -->
                              <tr v-bind:key="player.playerId" :class="key%2 !== 0 || 'alternative'">
                                  <td>{{key+1+(currentPage*itemsPerPage - itemsPerPage)}} <span v-if="env == 'development'">(<a :href="`https://api.opendota.com/api/players/${player.playerId}`">{{player.playerId}}</a>)</span></td>
                                  <td class="info">
                                      <span>
                                          {{player.playerId}}
                                      </span>
                                  </td>
                                  <template>
                                      <td>Profile Not Found</td>
                                  </template>
                                  <td>0</td>
                                  <td>0</td>
                              </tr>
                          </template>
                      </template>
                  </tbody>
              </table>
            </article>
            <footer>
              <div>
                  <span class="float_left">Page: </span>
                  <ul class="float_left">
                      <li v-for="pageNumber in totalPages" :key='pageNumber' class="paging">
                          <template v-if="Math.abs(pageNumber - currentPage) < 3 || pageNumber == totalPages || pageNumber == 1">
                              <a href="#/leaderboard" @click="setPage(pageNumber)" :class="{current: currentPage === pageNumber, last: (pageNumber == totalPages && Math.abs(pageNumber - currentPage) > 3), first:(pageNumber == 1 && Math.abs(pageNumber - currentPage) > 3)}">{{ pageNumber }}</a>
                          </template>
                      </li>
                  </ul>
              </div>
            </footer>
          </section>
        </div>
    </div>
</template>

<script>
import axios from "axios"
import config from "../config"
import * as func from "../helper/functions"
export default {
  name: "Leaderboard",
  data() {
    return {
      env: process.env.NODE_ENV,
      config: config,
      leaderboard: [],
      players: {},
      titleMessage: "",
      messages: ["Loading player data", "Killing dragons", "Rescuing prinesses", "Finding Easter Eggds", "Exploring dungeons"],
      loadingMessage: "",
      currentPage: 1,
      itemsPerPage: config.LEADERBOARD_ITEM_PER_PAGE,
      resultCount: 0,
      sortBy: "winRate",
      order: "desc",
      localPeriod: 7
    }
  },
  props: ["playerIds", "period", "useProData"],
  mounted: async function() {
    if (!this.$props.playerIds) {
      this.backHome()
    }
    this.titleMessage = this.messages[func.getRandomInt(0, this.messages.length - 1)]
    // display loading
    func.toggleLoading()
    axios.interceptors.response.use(
      response => {
        this.loadingMessage = `Receiving player ${response.config.playerId}`
        return response
      },
      error => {
        this.loadingMessage = `Failed receiving data`
        return Promise.reject(error)
      }
    )
    const { playerIds } = this
    // get player info
    const playerInfoResponses = await axios.all(
      playerIds.map(playerId =>
        axios.get(`${config.OPENDOTA_API}/players/${playerId}`, {
          playerId,
          validateStatus: status => {
            if (status == 429) {
              this.loadingMessage = `Failed receiving data`
            }
            return status < 500 // Reject only if the status code is greater than or equal to 500
          }
        })
      )
    )
    playerInfoResponses.forEach(response => {
      this.players[response.config.playerId] = {}
      this.players[response.config.playerId].info = response.data.profile ? response.data : null
    })
    this.localPeriod = this.period
    await this.getWinRate()
    // debugger
    func.toggleLoading()
  },
  computed: {
    totalPages: function() {
      return Math.ceil(this.resultCount / this.itemsPerPage)
    },
    paginate: function() {
      if (!this.playerIds || this.leaderboard.length != this.playerIds.length) {
        return
      }
      // debugger
      this.resultCount = this.leaderboard.length
      if (this.currentPage >= this.totalPages) {
        this.currentPage = this.totalPages
      }
      var index = this.currentPage * this.itemsPerPage - this.itemsPerPage
      return this.leaderboard.slice(index, index + this.itemsPerPage)
    },
    from: {
      get() {
        return this.localPeriod
      },
      set(period) {
        func.toggleLoading(async () => {
          this.localPeriod = period
          this.leaderboard = []
          await this.getWinRate()
          this.sort(this.sortBy, false)
          func.toggleLoading()
        })
        return period
      }
    }
  },
  methods: {
    toggleLoading(oncomplete) {
      $(".se-pre-con").fadeToggle("slow", oncomplete)
      $(".se-pre-con").toggleClass("hide")
    },
    setPage(pageNumber) {
      this.currentPage = pageNumber
    },
    backHome() {
      this.$router.push({ name: "Home" })
      return false
    },
    async getWinRate() {
      // get win loss
      const wlResponses = await axios.all(
        this.playerIds.reduce((requests, playerId) => {
          if (this.players[playerId].info) {
            requests.push(axios.get(`${config.OPENDOTA_API}/players/${playerId}/wl?date=${this.from}`, { playerId }))
          } else {
            this.leaderboard = func.insert({ playerId: playerId, winRate: -2, win: 0, lose: 0 }, this.leaderboard, "winRate")
          }
          return requests
        }, [])
      )
      wlResponses.forEach(response => {
        this.players[response.config.playerId].wl = response.data
        // build leaderboard
        let winRate
        if (response.data.win === 0 && response.data.lose === 0) {
          winRate = -1
        } else if (response.data.lose === 0) {
          winRate = 100
        } else {
          winRate = parseFloat((response.data.win / (response.data.win + response.data.lose) * 100).toFixed(3))
        }
        this.leaderboard = func.insert(
          {
            playerId: response.config.playerId,
            winRate,
            win: response.data.win,
            lose: response.data.lose
          },
          this.leaderboard,
          "winRate"
        )
      })
    },
    sort(type, reverseOrder = true) {
      this.sortBy = type
      if (this.order == "desc") {
        this.order = reverseOrder ? "asc" : this.order
      } else {
        this.order = reverseOrder ? "desc" : this.order
      }
      switch (type) {
        case "winRate":
          this.leaderboard = this.leaderboard.sort((a, b) => {
            return a.winRate - b.winRate
          })
          break
        case "won":
          this.leaderboard = this.leaderboard.sort((a, b) => {
            return a.win - b.win
          })
          break
        case "lost":
          this.leaderboard = this.leaderboard.sort((a, b) => {
            return a.lose - b.lose
          })
          break
        default:
          break
      }
      if (this.order == "desc") {
        this.leaderboard.reverse()
      } else {
        this.leaderboard = this.leaderboard.sort((a, b) => {
          if (a.winRate == -1 || a.winRate == -2) {
            return 1
          }
          if (b.winRate == -1 || b.winRate == -2) {
            return 1
          }
          return -1
        })
      }
    }
  },
  filters: {}
}
</script>

<style>

</style>



