export default {
  OPENDOTA_API: "https://api.opendota.com/api",
  // number of players to allow on one leaderboard
  // this also affect the rate limit of opendota.com which is about 3rps
  // which is also the limit that Valve allows https://steamcommunity.com/dev/apiterms
  GET_LIMIT: 10,
  // It's very likely to get blocked by opendota if GET_LIMIT is higher than 50
  API_RPS_LIMIT: 51,
  LEADERBOARD_ITEM_PER_PAGE: 10,
  // in case you have enough steam account id...
  USE_PRO_PLAYER_DATA: false
}



